@import url('https://fonts.googleapis.com/css?family=Roboto');
@import url('https://use.fontawesome.com/releases/v5.6.3/css/all.css');

body {
    font-family: 'Open Sans', sans-serif;
    color: #0f325a;
    margin: 0;
}

.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    min-height: 100vh;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.App main {
    -ms-flex: 1 1;
        flex: 1 1;
}

a {
    text-decoration: none;
}

.title {
    text-align: center;
    font-size: 0;
}

.site-title,
.site-desc,
.body-text {
    text-align: center;
    padding: 0;
    letter-spacing: 0.25rem;
    text-transform: uppercase;
}

.site-title {
    font-size: 2rem;
    margin: 1rem;
}

.site-desc {
    margin-bottom: 1rem;
}

.body-text {
    font-size: 1.1rem;
    letter-spacing: 0.125rem;
    margin: 2rem 0 0.75rem 0;
}

.titleForm {
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 10px;
    font-size: 1.75rem;
}

.form {
    margin: 0 auto;
    background: #0f325a;
    text-align: center;
    width: 80%;
    padding: 20px;
    color: white;
    font-size: 16px;
}

input {
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    background: none;
    outline: none;
    color:white;
    padding: 5px;
    margin: 20px;
    font: 16px 'Open Sans', sans-serif;
    border-bottom: 1px solid white;
    -webkit-transition: .5s ease-in;
    -o-transition: .5s ease-in;
    transition: .5s ease-in;
}

.inputModel {
    color: white;
    font: 16px 'Open Sans', sans-serif;
    width: 60%;
    padding-top: 5px;
    padding-bottom: 0;
    margin-bottom: 5px;
}
.inputPrice {
    color: white;
    font: 16px 'Open Sans', sans-serif;
    width: 60%;
}

input:focus{
    border-color: #FFF;
}

input[type=submit]{
    border: 1px solid white;
    padding: 10px 25px;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

input[type=submit]:hover{
    background: #00bbbb;
    border-color: #00bbbb;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

select  {
    width: 60%;
    outline: 0;
    overflow: hidden;
    height: 40px;
    background: #0f325a;
    color: #ffffff;
    border: none;
    padding: 5px 3px 5px 10px;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    -o-text-overflow: '';
       text-overflow: '';
    border-radius: 1px;
    border-bottom: 1px solid;
    border-color: white;
    cursor: pointer;
}

select option {
    border: 1px solid #000;
    background: #0f325a;
}
select option::-moz-selection {
    color: #ffffff;
}
select option::selection {
    color: #ffffff;
}

table{
    margin: 0 auto;
    padding: 10px;
}

table th {
    background: #DDD;
    padding: 10px;
}

table td {
    padding: 5px 10px;
    background: #EEE;
}

.secondaryButton{
    text-decoration: none;
    color: #0f325a;
    padding: 5px 10px;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.secondaryButton:hover{
    cursor: pointer;
}

@media screen and (max-width: 600px) {
    #adminView a {
        padding: 20px;
    }
}

/* Add to cart btn*/

#buttonAddToCart {
    text-align: center;
}

#buttonAddToCart {
    text-align: center;
}

/*Admin*/

#adminView {
    text-align: center;
}

#adminView a {
    font-weight: bold;
    font-family: Roboto, sans-serif;
    font-size: large;
    color: white;
    display: inline-block;
    margin: 20px;
    margin-top: 0;
}

#adminView a:hover {
    -webkit-filter:drop-shadow(0px 0px 7px red);
            filter:drop-shadow(0px 0px 7px red);
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
}

/*TOTAL ORDER PRICE*/
.totalOrderPrice {
    color: darkblue;
    font-style: italic;
    font-weight: bold;
}

.singleProductInTable {
    width: 70%;
}

#productNum {
    background-color: lightblue;
}

#productNumValue {
    background-color: lightblue;
}

#bg-img {
    background: url('../images/slideshow/image5.jpg');
    background-size: cover;
    background-position-y: center;
    padding: 13rem;
}

@media screen and (max-width: 600px) {
    #bg-img {
        padding: 8rem;
    }
}


