.viewAds {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 2rem auto;
}

.ads {
    max-width: 60rem;
    background: #0f325a;
    margin-bottom: 3.5rem;
}

.ads ul {
    padding: 2rem 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: 0;
    text-align: center;
}

.ads ul li {
    list-style-type: none;
    width: 28%;
    -ms-flex: 1 0 auto;
        flex: 1 0 auto;
    background: white;
    border: 2px solid white;
    border-radius: 3px;
    margin: 1rem;
}

.ads ul li:hover {
    -webkit-filter: drop-shadow(0 0 5px white);
            filter: drop-shadow(0 0 5px white);
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
}

.ads ul li .productBrand {
    font-family: Consolas, sans-serif;
    font-weight: bold;
    font-size: 1.3rem;
    border-bottom: 1px solid lightgray;
    color: darkblue;
    padding: 0.2rem 0;
}

.ads ul li .productActions {
    position: relative;
    z-index: 5;
}

.editLink,
.deleteLink {
    position: absolute;
    top: 0.15rem;
    border: none;
    border-radius: 3px;
    outline: none;
    font-size: 1rem;
    color: white;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.editLink {
    left: 0.15rem;
    padding: 0.1rem 0.25rem;
    background: blue;
}

.deleteLink {
    right: 0.15rem;
    padding: 0.1rem 0.342rem;
    background: red;
}

.editLink:hover,
.deleteLink:hover {
    background-color: white;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

.editLink:hover {
    color: blue;
}

.deleteLink:hover {
    color: red;
}

.ads ul li .productPicture {
    position: relative;
    overflow: hidden;
    background: transparent;
}

.ads ul li .productPicture:after {
    display: block;
    content: '';
    padding-top: 75%;
}

.ads ul li .productPicture img {
    display: block;
    width: 75%;
    position: absolute;
    top: 0;
    left: 0;
    height: auto;
    margin: 0 2.2rem;
}

.ads ul li .productModel,
.ads ul li .productInfo,
.ads ul li .productSize {
    color: #000;
    font-family: Consolas, sans-serif;
}

.ads ul li .productInfo {
    font-size: 0.9rem;
}

.ads ul li .productPrice {
    color: darkblue;
    font-family: Verdana, sans-serif;
    font-weight: bold;
    font-size: 1.15rem;
    border-top: 1px solid lightgray;
    padding: 0.3rem 0;
}

.noProductsPage {
    color: white;
    text-align: center;
}

@media screen and (max-width: 980px) {
    .ads ul li {
        width: 37%;
    }
}


@media screen and (max-width: 650px) {
    .ads ul li {
        width: 90%;
    }
}


#checkoutOrderBtn {
    margin-bottom: 0;
    width: 110px;
    font-weight: bold;
}

#deleteOrderBtn {
    margin-bottom: 0;
    width: 110px;
    font-weight: bold;
}


@media screen and (max-width: 750px) {
    #checkoutOrderBtn {
        margin-top: -10px;
        width: 30px;
    }

    #deleteOrderBtn {
        margin-top: -10px;
        width: 30px;
    }
}

@media screen and (max-width: 1250px) {
    #checkoutOrderBtn {
        width: 55px;
    }

    #deleteOrderBtn {
        width: 55px;
    }
}


