.cartAds {
    width: 90%;
    background: #0f325a;
    padding: 2rem 4rem;
}

.cartAds ul {
    padding: 0;
    margin: 0;
}

.cartAds ul li {
    list-style-type: none;
    margin: 0 auto 2rem auto;
    text-align: center;
    background: white;
}

.cartAds ul li:hover{
    -webkit-filter:drop-shadow(0px 0px 5px white);
            filter:drop-shadow(0px 0px 5px white);
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
}
.cartAds ul li .productBrand {
    font-family: Consolas, sans-serif;
    font-weight: bold;
    font-size: x-large;
    border-bottom: 1px solid lightgray;
    color: darkblue;
    padding: 0.3rem 0;
}

.cartAds ul li .productActions {
    position: relative;
    z-index: 5;
}

.cartAds ul li .productModel,
.cartAds ul li .productSize,
.cartAds ul li .productInfo {
    font-family: Consolas, sans-serif;
    color: #000;
}

.cartAds ul li .productCount {
    background: white;
    color: darkblue;
    font-weight: bold;
    border-top: 1px solid lightgray;
    padding: 0.3rem 0;
}

.cartAds ul li .productPrice {
    background: white;
    text-align: center;
    color: darkblue;
    font-weight: bold;
    border-top: 1px solid lightgray;
    padding: 0.3rem 0;
}

.cartTotalPrice {
    margin: 0 0 1rem 0;
}

.submitOrderForm {
    border: 2px solid white;
    text-align: center;
    color: white;
    font-family: Consolas, sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
    padding: 1rem 0;
}

.buyButton {
    border: 1px solid white;
    padding: 1rem 2rem;
    margin: 0;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    font-size: 1rem;
    font-weight: bold;
}

.noProductsPage {
    color: white;
    text-align: center;
}

@media screen and (max-width: 1250px) {
    .cartAds {
        width: 100%;
        padding: 0;
    }

    .cartAds ul li {
        width: 90%;
        margin: 2rem auto;
    }

    .cartAds ul li:last-child {
        margin-bottom: 0;
    }

    .cartAds .submitOrderForm {
        border: none;
    }
}


@media screen and (max-width: 750px) {
    .cartAds ul li {
        margin: 1rem auto;
    }

    .cartAds .submitOrderForm {
        margin: 1rem auto;
    }
}


